.header {
	padding-top: 70px;
}

.header-top {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 100;
	box-sizing: border-box;
	width: 100%;
	height: 70px;
	padding-top: 16px;
	background-color: #fff;
	transition: all .5s ease .01ms;
}

.header-top h1 {
	text-align: center;
}

.header-top h1 a {
	display: inline-block;
}

.header-top h1 img {
	height: 37px;
	transition: height .5s ease 0.01ms;
}

.header-top .btn-home {
	display: inline-block;
	position: absolute;
	top: 26px;
	left: 115px;
	color: #000;
	font-size: 14px;
	transition: top 0.5s ease 0.01ms;
}

.header-top .btn-home em {
	margin-left: 10px;
	vertical-align: middle;
}

.header-top .util {
	position: absolute;
	top: 26px;
	right: 30px;
	color: #000;
	font-size: 14px;
	transition: top 0.5s ease 0.01ms;
}

.header-top .util a {
	display: inline-block;
	margin-left: 20px;
	letter-spacing: -1px;
}

.header-top .util a em {
	margin-right: 10px;
	vertical-align: middle;
}

.header-megamenu .btn-megamenu {
	display: inline-block;
	position: absolute;
	top: 26px;
	left: 30px;
	color: #000;
	font-size: 14px;
	font-weight: 400;
	transition: top 0.5s ease 0.01ms;
}

.header-megamenu .btn-megamenu-text {
	vertical-align: middle;
}

.header-megamenu .btn-megamenu-closetext {
	display: none;
}

.header-megamenu .btn-megamenu .icon {
	position: relative;
	display: inline-block;
	width: 21px;
	height: 17px;
}

.header-megamenu .btn-megamenu .line {
	position: absolute;
	top: 50%;
	left: 0;
	display: block;
	width: 17px;
	height: 3px;
	margin-top: -1.5px;
	background-color: #323232;
	transform-origin: center;
	transition: transform 0.3s ease-in-out, margin-top 0.15s ease-in-out 0.3s, opacity 0.15s ease-in-out 0.3s, background 0.3s ease;
}

.header-megamenu .btn-megamenu .line-1 {
	transform: translateY(-6px);
}

.header-megamenu .btn-megamenu .line-2 {
	transition-delay: 0.2s;
}

.header-megamenu .btn-megamenu .line-3 {
	transform: translateY(6px);
}

.header-megamenu .btn-megamenu.btn-current-layer .line {
	transition: transform 0.3s ease-in-out, margin-top 0.15s ease-in-out, opacity 0.15s ease-in-out 0.1s;
}

.header-megamenu .btn-megamenu.btn-current-layer .line-1 {
	transform: rotate(135deg);
}

.header-megamenu .btn-megamenu.btn-current-layer .line-2 {
	opacity: 0;
}

.header-megamenu .btn-megamenu.btn-current-layer .line-3 {
	transform: rotate(225deg);
}

.header-megamenu .btn-megamenu.btn-current-layer-opentext {
	display: none;
}

.header-megamenu .btn-megamenu.btn-current-layer-closetext {
	display: inline-block;
}

.header .megamenu-menulist {
	display: none;
	position: fixed;
	top: 70px;
	bottom: 0;
	left: 0;
	overflow: auto;
	width: 100%;
	border-top: 1px solid #ccc;
	background-color: #f2f2f2;
}

.header .megamenu-menulist.active {
	display: block;
}

.header .megamenu-menubox {
	max-width: 1300px;
	margin: 0 auto;
	border-top: 1px solid #ccc;
}

.header .megamenu-menubox:after {
	content: '';
	display: block;
	clear: both;
}

.header .megamenu-menubox:first-child {
	border-top: none;
}

.header .megamenu-menubox-list1 {
	float: left;
	width: 25%;
	padding-top: 45px;
	padding-bottom: 45px;
	padding-left: 20px;
	color: #333;
	font-size: 20px;
	font-weight: 700;
	box-sizing: border-box;
}

.header .megamenu-dropdown {
	padding-top: 20px;
}

.header .megamenu-dropdown li a {
	color: #0061a0;
	font-size: 16px;
	font-weight: 400;
	line-height: 1.8;
}

.header .gnb-wrap {
	position: relative;
	height: 60px;
	background-color: #0061a0;
}

.header .gnb-wrap nav {
	max-width: 1200px;
	text-align: center;
	margin: 0 auto;
}

.header .gnb-wrap ul {
	display: inline-block;
}

.header .gnb-wrap ul:after {
	content: '';
	display: block;
	clear: both;
}

.header .gnb-wrap .gnb {
	float: left;
}

.header .gnb-wrap .depth-1 {
	position: relative;
	display: inline-block;
	width: 200px;
	height: 60px;
	line-height: 60px;
	color: #fff;
	text-align: center;
	vertical-align: top;
}

.header .gnb-wrap .depth-1 a {
	display: block;
	color: inherit;
	font-weight: 700;
	font-size: 16px;
}

.header .gnb-wrap .gnb.active .depth-1:before {
	content: '';
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 4px;
	background-color: #0090da;
}

.header .gnb-wrap .gnb:hover .depth-1 {
	background-color: #0090da;
}

.header .gnb-wrap .gnb:hover .depth-1:after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 50%;
	display: inline-block;
	margin-bottom: -7px;
	margin-left: -7px;
	border-bottom: 15px solid #fff;
	border-left: 15px solid #fff;
	transform: rotate(45deg);
}

.header .gnb-wrap .gnb:hover .depth-2 {
	display: block;
}

.header .gnb-wrap .depth-2 {
	position: absolute;
	left: 0;
	right: 0;
	display: none;
	height: 202px;
	background: #fff;
	z-index: 2;
}

.header .gnb-wrap .depth-2 ul {
	display: flex;
	max-width: 800px;
	width: 100%;
	margin: 0 auto;
	box-sizing: border-box;
}

.header .gnb-wrap .depth-2 ul:after {
	content: '';
	display: block;
	clear: both;
}

.header .gnb-wrap .depth-2 li {
	flex: 1;
	max-width: 200px;
}

.header .gnb-wrap .depth-2 li a {
	position: relative;
	display: block;
	height: 202px;
	text-align: center;
	font-size: 14px;
	font-weight: 700;
	color: #000;
	box-sizing: border-box;
}

.header .gnb-wrap .depth-2 li a:hover {
	color: #0090da;
	background-color: #fdfdfd;
}

.header .gnb-wrap .depth-2 li em {
	position: absolute;
	top: 46px;
	left: 50%;
	display: block;
	width: 56px;
	height: 54px;
	line-height: 54px;
	margin-left: -27px;
}

.header .gnb-wrap .depth-2 li span {
	display: block;
	padding-top: 120px;
}

.header .gnb-wrap .gnb-3 .depth-2 ul {
	padding-left: 200px;
}

.header .gnb-wrap .gnb-4 .depth-2 ul {
	max-width: 1200px;
}

.header .gnb-wrap .gnb-5 {
	display: none;
}

.header.fixed {
	background-color: #fff;
}

.header.fixed .header-top {
	height: 50px;
	padding-top: 0;
	box-shadow: rgba(0, 0, 0, 0.2) 0 2px 2px 0;
}

.header.fixed .header-top h1 {
	padding-top: 12px;
}

.header.fixed .header-top h1 img {
	width: 244px;
	height: 28px;
}

.header.fixed .header-top .btn-megamenu,
.header.fixed .header-top .btn-home,
.header.fixed .header-top .global-menu,
.header.fixed .header-top .util {
	top: 15px;
}

.header.fixed .megamenu-menulist {
	top: 50px;
}

.footer {
	padding-top: 65px;
	background-color: #f2f2f2;
}

.footer .inner {
	max-width: 1300px;
	margin: 0 auto;
}

.footer .link-list {
	position: relative;
	display: inline-block;
}

.footer .link-list .selectbox {
	font-size: 13px;
	display: inline-block;
	width: 215px;
	vertical-align: middle;
	overflow: visible;
	border: 0;
}

.footer .link-list .label {
	text-align: left;
	box-sizing: border-box;
	display: block;
	width: 100%;
	height: 100%;
	padding-right: 40px;
	padding-left: 20px;
	border: 1px solid #ccc;
}

.footer .link-list ul {
	display: none;
}

.footer .link-list.active ul {
	display: block;
	position: absolute;
	top: 100%;
	left: 0;
	z-index: 10;
	box-sizing: border-box;
	width: 100%;
	margin-top: -1px;
	border: 1px solid #ccc;
	background-color: #fff;
}

.footer .link-list.active ul a {
	display: block;
	height: 100%;
	padding-right: 40px;
	padding-left: 20px;
}

.footer .link-list.active ul a:hover {
	background-color: #f4f4f4;
}

.footer-menu-list {
	display: table;
	width: 100%;
	max-width: 1300px;
	margin: 0 auto 65px;
}

.footer-menu-list:after {
	content: '';
	display: block;
	clear: both;
}

.footer-menu-list .menu-item {
	display: table-cell;
	width: 25%;
	padding-left: 20px;
	box-sizing: border-box;
}

.footer-menu-list .menu-item strong {
	display: block;
	margin-bottom: 15px;
	font-size: 18px;
	font-weight: 700;
	line-height: 1.5;
}

.footer-menu-list .menu-item li a {
	color: #0061a0;
	font-size: 15px;
	line-height: 1.8;
}

.footer-site {
	position: relative;
	margin-bottom: 40px;
}

.footer-site .sns {
	position: absolute;
	bottom: 0;
	right: 50px;
}

.footer-site .sns a {
	display: inline-block;
	margin-left: 15px;
	font: 0/0 a;
}

.footer-info {
	padding-bottom: 75px;
	border-top: 1px solid #ccc;
}

.footer-info:after {
	content: '';
	display: block;
	clear: both;
}

.footer-logo {
	float: left;
	margin-top: 75px;
	margin-right: 68px;
}

.footer-etc {
	float: left;
	margin-top: 40px;
	font-size: 14px;
	line-height: 1.7;
}

.footer-etc .link-list .label {
	display: none;
}

.footer-etc .link-list ul {
	display: block;
}

.footer-etc .link-policy {
	margin-bottom: 13px;
}

.footer-etc .link-policy:after {
	content: '';
	display: block;
	clear: both;
}

.footer-etc .link-policy li {
	float: left;
	margin-right: 25px;
}

.footer-etc .link-policy li a {
	color: #0061a0;
	line-height: 2.1;
}

.footer-award {
	position: relative;
	float: right;
	margin-top: 67px;
}

.footer-award .icon-award {
	position: absolute;
	top: 50%;
	left: 0;
	margin-top: -26px;
}

.footer-award span {
	display: inline-block;
	padding-left: 49px;
	font-size: 12px;
	line-height: 1.5;
	color: #666;
}
